/*====================== Pagination Style ====================*/
.pagination {
    flex-wrap: wrap;
    justify-content: center;

    .page-item {
        .page-link {
            border: 1px solid hsl(var(--base) / .08);
            margin: 0 5px;
            border-radius: 50%;
            height: 40px;
            width: 40px;
            display: flex;
            justify-content: center;
            align-items: center;
            background-color: transparent;
            font-weight: 500;
            padding: 0;
            color: hsl(var(--black));
            cursor: pointer;

            &.active,
            &:hover {
                background-color: hsl(var(--base));
                color: hsl(var(--white));
            }

            &:focus {
                box-shadow: none;
            }
        }

        &.active .page-link {
            background-color: hsl(var(--base));
            color: hsl(var(--white));
        }
    }
}